<template lang="pug">
	#app
		MenuNav(:links="links")
		main
			button(@click="clickMenu") menu
			router-view
</template>

<script>
	import MenuNav from '@/components/MenuNav';

	export default {
		name: 'App',
		components: {
			MenuNav
		},
		data: () => ({
			showMenu: false,
			links: [
				{
					url: '/',
					text: 'Horarios',
					icon: 'calendar_today'
				},
				{
					url: '/people',
					text: 'Ponentes',
					icon: 'supervisor_account'
				},
				{
					url: '/location',
					text: 'Localización',
					icon: 'location_on'
				},
				{
					url: '/info',
					text: 'Info',
					icon: 'help_outline'
				},
			],
		}),
		methods: {
			clickMenu(){
				this.showMenu = true
				this.$store.commit('setMenuNav', this.showMenu)
			},
		}
	};
</script>

<style lang="stylus" src="@/styles/main.styl">
</style>
